html {
  box-sizing: border-box;
}
body {
  min-height: 100vh;
  margin: 0;
}
*,
*::before,
*::after {
  box-sizing: inherit;
  padding: 0px;
  margin: 0px;
}
.search{
  margin-left: auto;
  margin-right: auto;
}
input{
  width: 250px;
  height: 35px;
  border-radius: 5px;
  margin-bottom: 25px;
}
.example {

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  background-color: #fff;
  color: #424242;

  padding: 16px;
}

.gallery{

  display: flex;
  flex-wrap: wrap;
}
li {
  margin-left: auto;
margin-right: auto;

  list-style-type: none;
}
.stats {
  display: flex;
  justify-content: space-evenly;
}
img {
  display: block;
  max-width: 400px;
  height: auto;
}

.load-button{
  margin-top: 25px;
  width: 150px;
  height: 35px;
  background-color: rgb(0, 255, 34);
  border: none;
  border-radius: 5px;
  font-weight: 700;
  font-size: 20px;
}